import { useState, useContext } from 'react';

import { Box, Button, Card, CardContent, Typography } from '@mui/material';
import TextField from '@mui/material/TextField';

import { AppContext } from "../App";

const ClassCard = () => {
  const { classNameContext, studentListContext } = useContext(AppContext);
  const [className, setClassName] = classNameContext;
  const [studentList, setStudentList] = studentListContext;

  const [editingClassName, setEditingClassName] = useState(false);
  const [editingStudentList, setEditingStudentList] = useState(false);

  let studentListString = "";
  for (let i = 0; i < studentList.length; i++) {
    studentListString += studentList[i].name + (i < studentList.length - 1 ? "\n" : "");
  }

  const handleChangeStudentList = (event) => {
    const studentArray = event.target.value.split("\n");
    let newStudentList = [];
    for (let i = 0; i < studentArray.length; i++) {
      const s = { id: i, name: studentArray[i]}
      newStudentList.push(s);
    }
    setStudentList(newStudentList);
    localStorage.setItem("studentList", JSON.stringify(newStudentList));
  };

  return (
    <Card elevation={2} sx={{margin: "20px 5px 0 5px", width: "450px", outline: "1px solid gainsboro"}}>
      <CardContent>
        <Box p={2} sx={{border: "1px solid lightgray", borderRadius: "4px", "&.MuiBox-root": {margin: "0"}}}>
          <Typography variant={"h6"} component={"h6"}>
            Class Name
          </Typography>
          { editingClassName
            ? <Box p={1} sx={{"&.MuiBox-root": {margin: "2px"}}}>
              <TextField
                id="class-name"
                variant="outlined"
                size="small"
                value={className}
                sx={{minWidth: "250px"}}
                inputProps={{style: {fontSize: "0.875rem"}}}
                onChange={(event) => {
                  setClassName(event.target.value);
                  localStorage.setItem("className", event.target.value);
                }}
              />
              <Button variant="outlined" sx={{minWidth: "80px", height: "30px", margin: "4px 4px 4px 8px"}} onClick={() => {
                setEditingClassName(false);
              }}>done</Button>
            </Box>
          :<Box p={1} sx={{"&.MuiBox-root": {margin: "0px"}}} display="flex" alignItems="center">
            <Typography variant={"body2"} component={"div"} sx={{minWidth: "250px", borderBottom: "1px solid"}}>
            {className}
            </Typography>
            <Button variant="outlined" sx={{minWidth: "80px", height: "30px", margin: "4px 4px 4px 8px"}} onClick={() => {
              setEditingClassName(true);
            }}>edit</Button>
          </Box>
          }
        </Box>
        <Box p={2} sx={{border: "1px solid lightgray", borderRadius: "4px", "&.MuiBox-root": {margin: "4px 0 0 0"}}}>
          <Typography variant={"h6"} component={"h6"}>
            Students
          </Typography>
          { editingStudentList
            ? <Box p={1} sx={{"&.MuiBox-root": {margin: "2px"}}} display="flex" alignItems="flex-end">
              <TextField
                id="student-list"
                variant="outlined"
                multiline
                maxRows={studentList.length}
                sx={{minWidth: "250px"}}
                inputProps={{style: {fontSize: "0.875rem"}}}
                value={studentListString}
                onChange={(event) => {
                  handleChangeStudentList(event)
                }}
              />
              <Button variant="outlined" sx={{minWidth: "80px", height: "30px", margin: "4px 4px 4px 8px"}} onClick={() => {
                setEditingStudentList(false);
              }}>done</Button>
            </Box>
          :<Box p={1} sx={{"&.MuiBox-root": {margin: "2px"}}} display="flex" alignItems="flex-end">
            <Typography variant={"body2"} component={"div"} sx={{minWidth: "250px", borderBottom: "1px solid", whiteSpace: "pre-line"}}>
              {studentListString}
            </Typography>
            <Button variant="outlined" sx={{minWidth: "80px", height: "30px", margin: "4px 4px 4px 8px"}} onClick={() => {
              setEditingStudentList(true);
            }}>edit</Button>
          </Box>
          }
        </Box>
      </CardContent>
    </Card>
  );
}

export default ClassCard;