import React from 'react';
import { useContext } from 'react';
import { useNavigate } from 'react-router-dom';

import { Box, Button, Card, CardContent, Container, Typography } from '@mui/material';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';

import TopBar from '../components/TopBar';

import { AppContext } from "../App";

export default function Home() {
  let navigate = useNavigate();
  const navigateToStart = () =>{
    let path = `/start`;
    navigate(path);
  }
  const navigateToRubric = () =>{
    let path = `/rubric/${currentRubricID}`;
    navigate(path);
  }

  const { currentRubricIDContext, rubricsContext } = useContext(AppContext);
  const [rubrics] = rubricsContext;
  const [currentRubricID, setCurrentRubricID] = currentRubricIDContext;
  const handleChangeRubric = (event) => {
    setCurrentRubricID(event.target.value);
  };

  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline: "1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"}>
              SCALE Science Rubrics
              </Typography>
              <Box m={2}>
                <Typography variant={"body1"} component={"span"}>
                This is an in-progress version of the SCALE Science Rubrics website.
                To begin, create a class name and a student list. Then select a rubric to start entering data (Rubric 1 is the only rubric at present with real content).
                Session information is saved to and restored from your local browser storage so any class, student, or rubric information that you previously entered will
                be restored each time you visit this site.
                </Typography>
              </Box>
              <Box m={5} display="flex" justifyContent="center" alignItems="flex-end" sx={{flexWrap: "wrap"}}>
                <Box display="flex" flexDirection="column" justifyContent="center" m={2} p={2} sx={{border: "1px solid lightgray", borderRadius: "4px"}}>
                  <Typography variant={"body2"} component={"div"} sx={{width: "220px", margin: "10px 20px 10px 20px", textAlign:"center"}}>First enter a class name, a student list, and select a rubric.</Typography>
                  <Button
                    variant="outlined"
                    sx={{width: "220px", margin: "10px 20px 10px 20px"}}
                    onClick={navigateToStart}
                  >
                    GET STARTED
                  </Button>
                </Box>

                <Box display="flex" flexDirection="column" justifyContent="center" m={2} p={2} sx={{border: "1px solid lightgray", borderRadius: "4px"}}>
                  <Typography variant={"body2"} component={"div"} sx={{width: "220px", margin: "10px 20px 10px 20px", textAlign:"center"}}>
                    If you have already entered class information, choose a rubric to continue entering data.
                  </Typography>
                  <Box
                    component="form"
                    sx={{
                      '& > :not(style)': { m: 1, width: '45ch' },
                    }}
                    display="flex"
                    justifyContent="center"
                  >
                    <FormControl style={{maxWidth: "220px"}} m={0}>
                      <InputLabel id="rubric-select-label">Rubric</InputLabel>
                      <Select
                        labelId="rubric-select-label"
                        id="rubric-select"
                        value={currentRubricID}
                        label="Rubric"
                        onChange={handleChangeRubric}
                      >
                        {rubrics.map((r, i) =>
                          <MenuItem key={`rubric${i}`} value={r.r_id}>{r.name}</MenuItem>
                        )}
                      </Select>
                    </FormControl>
                  </Box>
                  <Button
                    variant="outlined"
                    sx={{width: "220px", margin: "10px 20px 10px 20px"}}
                    onClick={navigateToRubric}
                  >
                    ENTER RUBRIC DATA
                  </Button>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}