import React from 'react';

import { Box, Card, CardContent, Container, Typography } from '@mui/material';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Stack from '@mui/material/Stack';
import TopBar from '../components/TopBar';

const aboutText = [
  "Loads 1 or more rubrics stored as a structured JSON file",
  "Allows the user to enter a class name",
  "Allows the user to enter a student list",
  "Allows the user to select a rubric",
  "Displays selected rubric content to the user",
  "Allows the user to view a single dimension or all dimensions at once",
  "Allows the user to view a single dimension or all dimensions at once",
  "Allows the user to enter student-specific scoring and notes for each rubric",
  "Saves and restores the user session including the class name, student list, and rubric input",
];

export default function About() {
  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline: "1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"}>
              About
              </Typography>
              <Box m={2}>
                <p>This is an in-progress version of the SCALE Science Rubrics website. At present the site does the following:</p>
                <List sx={{ listStyleType: 'disc' }}>
                  {aboutText.map((at, i) =>
                    <ListItem key={i} sx={{ marginLeft: "30px", display: 'list-item' }}>{at}</ListItem>
                  )}
                </List>
              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}