import React from 'react';

import { Card, CardContent, Container, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Stack from '@mui/material/Stack';

import TopBar from '../components/TopBar';

const faqText = [
  {summary: "How can I see my data from a different computer?", content: "coming soon..."},
  {summary: "Why can’t I see my entries from a previous session?", content: "coming soon..."},
  {summary: "How do I assign scores to my students?", content: "coming soon..."},
  {summary: "Can I print or download my class data?", content: "coming soon..."},
  {summary: "How do I communicate about the data from the rubric to students and caregivers?", content: "coming soon..."},
]

export default function FAQ() {
  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline: "1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"}>
              FAQ
              </Typography>
              {faqText.map((ft, i) =>
                <Accordion key={`faq-${i}`} disableGutters>
                  <AccordionSummary
                    key={`faq-as-${i}`}
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="faq-content"
                    sx={{fontWeight: "500"}}
                  >
                    {ft.summary}
                  </AccordionSummary>
                  <AccordionDetails key={`faq-ad-${i}`} sx={{whiteSpace: "pre-line"}}>
                    {ft.content}
                  </AccordionDetails>
                </Accordion>
              )}
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}