import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import parse from 'html-react-parser';

import { Box, Container, Card, CardContent, Button, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Checkbox from '@mui/material/Checkbox';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';

import { imageData } from '../image-utils';
import { dimensionColors, dimensionNames, dimensionFullNames, convertEntitiesToElements } from '../utils';

import { AppContext } from '../App';
import DimensionBlock from '../components/DimensionBlock';
import GroupSummary from '../components/GroupSummary';
import IndividualSummary from '../components/IndividualSummary';
import TopBar from '../components/TopBar';

export default function Rubric() {
  const { currentRubricIDContext, rubricsContext, studentListContext } = useContext(AppContext);
  const [rubrics] = rubricsContext;
  const [studentList] = studentListContext;
  const [currentRubricID, setCurrentRubricID] = currentRubricIDContext;

  const [allDimensions, setAllDimensions] = useState(false);
  const [showGroupSummary, setShowGroupSummary] = useState(false);
  const [showIndividualSummary, setShowIndividualSummary] = useState(false);

  let { id } = useParams();
  useEffect(() => {
    if (currentRubricID !== id) {
      setCurrentRubricID(id);
    }
  }, [id, setCurrentRubricID, currentRubricID]);
  const currentRubric = rubrics.find((r) => r.r_id === currentRubricID);

  const [currentDimensionTab, setCurrentDimensionTab] = React.useState(0);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [currentStudentID, setCurrentStudentID] = useState(studentList[0].id);

  useEffect(() => {
    setCurrentStudentID(studentList[0].id);
  }, [studentList]);

  useEffect(() => {
    if (currentQuestionIndex >= currentRubric.questions.length) {
      setCurrentQuestionIndex(0);
    }
  }, [currentRubric, currentQuestionIndex]);

  //const currentRubricID = currentRubric.r_id;
  const currentQuestionIndexToUse = currentQuestionIndex >= currentRubric.questions.length ? 0 : currentQuestionIndex;
  const currentQuestion = currentRubric.questions[currentQuestionIndexToUse];
  const currentQuestionID = currentRubric.questions[currentQuestionIndexToUse].q_id;
  const currentDimension = currentRubric.questions[currentQuestionIndexToUse].dimensions[currentDimensionTab];
  const currentDimensionID = currentDimension?.d_id;
  const currentDimColor = dimensionColors[currentDimensionTab];

  function tabProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
      sx: {backgroundColor: dimensionColors[index],
        color: "white",
        borderRadius: "10px 10px 0 0",
        marginTop: "6px",
        "&.Mui-selected": {color: "white", marginTop: 0}
      }
    };
  };

  const handleChangeTab = (event, newTabValue) => {
    setCurrentDimensionTab(newTabValue);
  };

  const handleChangeStudent = (event) => {
    setCurrentStudentID(event.target.value);
  };

  const handleSelectQuestion = (index) => {
    setCurrentQuestionIndex(index);
    setShowGroupSummary(false);
    setShowIndividualSummary(false);
  }
  const handleSelectGroupSummary = () => {
    setShowGroupSummary(true);
    setShowIndividualSummary(false);
  };
  const handleSelectIndividualSummary = () => {
    setShowGroupSummary(false);
    setShowIndividualSummary(true);
  }

  return (
    <div>
      <TopBar showClassSelection={true} showRubricSelection={true} />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={1}
        >
          {!showIndividualSummary && !showGroupSummary &&
            <Box sx={{"&.MuiBox-root": {margin: "20px 4px 4px 4px"}}}>
              <FormControl sx={{width: "400px"}}>
                <InputLabel id="student-select-label">Current Student</InputLabel>
                <Select
                  labelId="student-select-label"
                  id="student-select"
                  value={currentStudentID}
                  label="Current Student"
                  onChange={handleChangeStudent}
                >
                  {studentList.map((s, i) =>
                    <MenuItem key={`student${i}`} value={s.id}>{s.name}</MenuItem>
                  )}
                </Select>
              </FormControl>
            </Box>
          }

          <Box component="fieldset"
            display="flex"
            alignItems="center"
            justifyContent="flex-start"
            sx={{ width: "100%", padding: "6px 12px 6px 12px", border: "1px solid lightgray", borderRadius: "4px", flexWrap: "wrap", "&.MuiBox-root": {margin: "4px"}}}
          >
            <legend style={{fontWeight: "400", fontSize: ".75rem", color: "rgba(0, 0, 0, .6)"}}>Questions and Summary</legend>
            {currentRubric.questions.map((q, i) =>
              <Button key={`questions${i}`} onClick={() => handleSelectQuestion(i)}>{q.short_name}</Button>
            )}
            <Button key={`group-summary`} onClick={handleSelectGroupSummary}>GPL</Button>
            <Button key={`individual-summary`} onClick={handleSelectIndividualSummary}>IPL</Button>
          </Box>

          {showGroupSummary && <GroupSummary performanceLevel={currentRubric.group_performance_level} />}
          {showIndividualSummary && <IndividualSummary performanceLevel={currentRubric.individual_performance_level} />}
          {!showIndividualSummary && !showGroupSummary &&
            <Box sx={{"&.MuiBox-root": {margin: "0"}}}>
              <Box sx={{width: "100%", border: "1px solid lightgray", borderRadius: "4px", "&.MuiBox-root": {margin: "4px"}}}>
                <Accordion disableGutters>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1-content"
                    id="panel1-header"
                    sx={{fontWeight: "500", "&.MuiAccordionSummary-root": {minHeight: "40px", margin: "0"}, "&.MuiAccordionSummary-content": {margin: "0"}}}
                  >
                    {currentQuestion.name}
                  </AccordionSummary>
                  <AccordionDetails sx={{padding:"4px 16px 8px 16px", fontSize:"14px", whiteSpace: "pre-line"}}>
                    {parse(convertEntitiesToElements(currentQuestion.question_content))}
                  </AccordionDetails>
                </Accordion>
                <Accordion disableGutters>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2-content"
                    id="panel2-header"
                    sx={{fontWeight: "500", "&.MuiAccordionSummary-root": {minHeight: "40px", margin: "0"}, "&.MuiAccordionSummary-content": {margin: "0"}}}
                  >
                    Performance Outcome
                  </AccordionSummary>
                  <AccordionDetails sx={{padding:"4px 16px 8px 16px", fontSize:"14px", whiteSpace: "pre-line"}}>
                    {parse(convertEntitiesToElements(currentQuestion.performance_outcomes))}
                  </AccordionDetails>
                </Accordion>
                <Accordion disableGutters>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel3-content"
                    id="panel3-header"
                    sx={{fontWeight: "500", "&.MuiAccordionSummary-root": {minHeight: "40px", margin: "0"}, "&.MuiAccordionSummary-content": {margin: "0"}}}
                  >
                    Student Sample Response
                  </AccordionSummary>
                  <AccordionDetails sx={{padding:"4px 16px 8px 16px", fontSize:"14px", whiteSpace: "pre-line"}}>
                    {parse(convertEntitiesToElements(currentQuestion.student_example))}
                    {currentQuestion.student_example_image && <img src={imageData[currentQuestion.student_example_image]} alt="student sample" />}
                  </AccordionDetails>
                </Accordion>
              </Box>

              <Box display="flex" sx={{"&.MuiBox-root": {margin: "0"}}}>
                {!allDimensions && <Tabs
                  sx={{"&.MuiTabs-root": {margin: "4px 4px 0 4px"}}}
                  TabIndicatorProps={{style: {backgroundColor: "yellow", height: "3px"}}}
                  value={currentDimensionTab}
                  onChange={handleChangeTab}
                  aria-label="dimension tabs"
                >
                  <Tab label={dimensionNames[0]} {...tabProps(0)} />
                  <Tab label={dimensionNames[1]} {...tabProps(1)} />
                  <Tab label={dimensionNames[2]} {...tabProps(2)} />
                </Tabs> }
                <Box display="flex" alignItems="center" sx={{"&.MuiBox-root": {margin: "0", marginLeft: "auto"}}}>
                  <Checkbox
                    checked={allDimensions}
                    inputProps={{ 'aria-label': 'controlled' }}
                    onChange={(event) => {
                      setAllDimensions(!allDimensions);
                    }}
                  />
                  <Typography>Show All Dimensions</Typography>
                </Box>
              </Box>

              <Box sx={{borderBottom: 1, borderColor: "divider", "&.MuiBox-root": {marginTop: "0"}}}>
                <Card
                  elevation={2}
                  sx={{outline: "1px solid gainsboro", border: `3px solid ${allDimensions ? "divider" : currentDimColor}`, borderRadius: "0 0 4px 4px"}}
                >
                  <CardContent>
                    {currentQuestion.dimensions.map((d, i) =>
                      (allDimensions || d.d_id === currentDimensionID) &&
                        <DimensionBlock
                          currentStudentID={currentStudentID}
                          currentRubricID={currentRubricID}
                          currentQuestionID={currentQuestionID}
                          dimensionID={d.d_id}
                          dimension={d}
                          dimName={dimensionFullNames[i]}
                          dimColor={dimensionColors[i]}
                          highlight={allDimensions}
                          key={i}
                        />
                    )}
                  </CardContent>
                </Card>
              </Box>
            </Box>
          }

        </Stack>
      </Container>
    </div>
  )
}
