import { createContext, useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

import { decompress as lzStringDeCompress } from 'lz-string';

import Home from './pages/Home';
import About from './pages/About';
import FAQ from './pages/FAQ';
import ClassManager from './pages/ClassManager';
import Rubric from './pages/Rubric';

import r1 from './data/rubric1.json';
import r2 from './data/rubric2.json';
import r3 from './data/rubric3.json';

const rubricList = [r1, r2, r3];
const defaultStudentList = [{id: 0, name: "student a"}, {id: 1, name: "student b"}, {id: 2, name: "student c"}];
export const RubricContext = createContext();
export const AppContext = createContext();

function App() {
  const [rubrics, setRubrics] = useState(rubricList);
  const [className, setClassName] = useState("my class");
  const [studentList, setStudentList] = useState(defaultStudentList);
  const [currentRubricID, setCurrentRubricID] = useState(rubricList[0].r_id);
  const [currentUserInput, setCurrentUserInput] = useState({});

  useEffect(() => {
    const storedClassName = localStorage.getItem("className");
    if (storedClassName) setClassName(storedClassName);
    const storedStudentList = localStorage.getItem("studentList");
    const studentListJSON = JSON.parse(storedStudentList);
    if (studentListJSON) setStudentList(studentListJSON);
    // now that we have the student list AND the rubrics,
    // we can make a object to hold the user input for each student
    const currStudentList = studentListJSON ? studentListJSON : defaultStudentList;
    let userInput = { sts: {} };
    for (let s = 0; s < currStudentList.length; s++) {
      const sitem = currStudentList[s];
      userInput.sts[sitem.id] = {};
      for (let r = 0; r < rubricList.length; r++) {
        const ritem = rubricList[r];
        userInput.sts[sitem.id][ritem.r_id] = {};
        for (let q = 0; q < ritem.questions.length; q++) {
          const qitem = ritem.questions[q];
          userInput.sts[sitem.id][ritem.r_id][qitem.q_id] = {};
          for (let d = 0; d < qitem.dimensions.length; d++) {
            const ditem = qitem.dimensions[d];
            userInput.sts[sitem.id][ritem.r_id][qitem.q_id][ditem.d_id] = { n: "" };
            for (let p = 0; p < ditem.description_performance.points.length; p++) {
              const pitem = ditem.description_performance.points[p];
              userInput.sts[sitem.id][ritem.r_id][qitem.q_id][ditem.d_id][pitem.p_id] = { v: 0};
            }
          }
        }
      }
    }
    // Now we can grab the user input from local storage
    // and use it to populate our object
    const storedUserInput = lzStringDeCompress(localStorage.getItem("userInput"));
    if (storedUserInput) {
      const storedUserInputJSON = JSON.parse(storedUserInput);
      const studentList = storedUserInputJSON.sts;
      for (const s in studentList) {
        const sitem = studentList[s];
        for (const r in sitem) {
          const ritem = sitem[r];
          for (const q in ritem) {
            const qitem = ritem[q];
            for (const d in qitem) {
              const ditem = qitem[d];
              for (const p in ditem) {
                const pitem = ditem[p];
                const userInputPitemVal = userInput.sts?.[s]?.[r]?.[q]?.[d]?.[p]?.v;
                if (userInputPitemVal === 0) {
                  userInput.sts[s][r][q][d][p].v = pitem.v;
                }
              }
              const userInputNotes = userInput.sts?.[s]?.[r]?.[q]?.[d]?.n;
              if (userInputNotes !== null && userInputNotes !== undefined) {
                userInput.sts[s][r][q][d].n = ditem.n;
              }
            }
          }
        }
      }
    }
    setCurrentUserInput(userInput);
  }, []);

  return (
    <AppContext.Provider
      value={{
        rubricsContext: [rubrics, setRubrics],
        classNameContext: [className, setClassName],
        studentListContext: [studentList, setStudentList],
        currentRubricIDContext: [currentRubricID, setCurrentRubricID],
        currentUserInputContext: [currentUserInput, setCurrentUserInput],
      }}
    >
      <Router>
        <Routes>
          <Route exact path="/" element={<Home/>} />
          <Route exact path="/start" element={<ClassManager/>} />
          <Route exact path="/about" element={<About/>} />
          <Route exact path="/faq" element={<FAQ/>} />
          <Route exact path="/rubric/:id" element={<Rubric/>} />
        </Routes>
      </Router>
    </AppContext.Provider>
  );
}

export default App;
