import React, { useContext } from 'react';

import parse from 'html-react-parser';
import { compress as lzStringCompress } from 'lz-string';

import { Box, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Checkbox from '@mui/material/Checkbox';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import TextField from '@mui/material/TextField';

import { AppContext } from '../App';

import { convertEntitiesToElements } from '../utils';

const DimensionBlock = ({currentStudentID, currentRubricID, currentQuestionID, dimensionID, dimension, dimName, dimColor, highlight}) => {
  const { currentUserInputContext } = useContext(AppContext);
  const [currentUserInput, setCurrentUserInput] = currentUserInputContext;

  const handleChangeCheckbox = (event, pointID) => {
    let updatedCurrentUserInput = { ...currentUserInput };
    const updatedPoint = event.target.checked ? "1" : "0";
    currentUserInput.sts[currentStudentID][currentRubricID][currentQuestionID][dimensionID][pointID].v = updatedPoint;
    setCurrentUserInput(updatedCurrentUserInput);

    localStorage.setItem("userInput", lzStringCompress(JSON.stringify(updatedCurrentUserInput)));
  };

  const handleChangeNotes = (event) => {
    let updatedCurrentUserInput = { ...currentUserInput };
    const updatedNotes = event.target.value;
    currentUserInput.sts[currentStudentID][currentRubricID][currentQuestionID][dimensionID].n = updatedNotes;
    setCurrentUserInput(updatedCurrentUserInput);
    localStorage.setItem("userInput", lzStringCompress(JSON.stringify(updatedCurrentUserInput)));
  };

  return (
    <Box sx={{marginBottom: "4px", border: `2px solid ${highlight ? dimColor : "divider"}`, borderRadius: "4px"}}>
      <Accordion disableGutters>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="dimension-alignment-content"
          id="dimension-alignment-header111"
          sx={{fontWeight: "500", "&.MuiAccordionSummary-root": {minHeight: "36px", margin: "0"}, "&.MuiAccordionSummary-content": {margin: "0"}}}
        >
          {`Dimension Alignment: ${dimName}`}
        </AccordionSummary>
        <AccordionDetails sx={{padding:"4px 16px 8px 16px", fontSize:"14px", whiteSpace: "pre-line"}}>
          {parse(convertEntitiesToElements(dimension.dimension_alignment))}
        </AccordionDetails>
      </Accordion>
      <Box
        p={2}
        sx={{ width: "100%", border: "1px solid lightgray", borderRadius: "4px", margin: "6px 0 0 0"}}
      >
        { dimension ?
        <>
          <Typography variant={"body2"} component={"div"}>
            {parse(convertEntitiesToElements(dimension.description_performance.prompt))}
          </Typography>
          {dimension.description_performance.points.map((p) =>
            <Box
              key={`si${currentStudentID}-qi${currentQuestionID}-di${dimensionID}-point${p.p_id}`}
              sx={{margin: "0 0 4px 0"}}
              display="flex"
              alignItems={'flex-start'}
            >
              <Checkbox
                key={`si${currentStudentID}-qi${currentQuestionID}-di${dimensionID}-point${p.p_id}`}
                sx={{padding: "0 4px 0 0"}}
                checked={currentUserInput?.sts?.[currentStudentID]?.[currentRubricID]?.[currentQuestionID]?.[dimensionID]?.[p.p_id]?.v === "1"}
                inputProps={{ 'aria-label': 'controlled' }}
                onChange={(event) => {
                  handleChangeCheckbox(event, p.p_id);
                }}
              />
              <Box sx={{padding: "2px 0 0 0"}}>
                <Typography variant={"body2"} component={"div"}>
                  {parse(convertEntitiesToElements(p.content))}
                </Typography>
              </Box>
            </Box>
          )}
          <TextField
            id="notes"
            label="notes"
            variant="outlined"
            value={currentUserInput?.sts?.[currentStudentID]?.[currentRubricID]?.[currentQuestionID]?.[dimensionID]?.n || ""}
            sx={{width: "100%", margin:"12px 0 0 0"}}
            size={"small"}
            onChange={(event) => {
              handleChangeNotes(event);
            }}
          />
        </>
        : <Box sx={{minHeight: "100px"}}>{`No ${dimName} content for this question.`}</Box>
        }
      </Box>
    </Box>
  );
}

export default DimensionBlock;